<template>
  <v-responsive id="location_map" class="custom-content-container">
    <div class="divider-bar"></div>
    <div class="text-container-1 text-type-black text-type-16 text-align-left">
      {{ $t("view.locationMap.description") }}
    </div>
    <div class="text-type-olive text-type-20">
      {{ $t("view.locationMap.date") }}
    </div>
    <div class="text-type-olive text-type-20">
      {{ $t("view.locationMap.location") }}
    </div>
    <div class="text-type-olive text-type-20">
      {{ $t("view.locationMap.location2") }}
    </div>
    <div class="map-container" ref="mapContainer"></div>
    <v-responsive class="d-flex button-container">
      <v-row class="" justify="space-between" cols="12">
        <v-col class="col-6">
          <!-- <v-btn
            class="elevation-0 custom-button"
            block
            :large="true"
            color="primary"
            v-if="isInitializeGoogle"
            v-on:click="onClickCalendarEvent"
          >
            {{ $t("view.locationMap.addCalendar") }}
          </v-btn>-->
        </v-col>
        <v-col class="col-6">
          <v-btn
            class="elevation-0 custom-button"
            block
            color="primary"
            :large="true"
            href="https://map.kakao.com/?urlX=794834.0&urlY=727681.0&name=%EA%B8%88%EC%98%A4%EC%82%B0%ED%98%B8%ED%85%94"
            target="_blank"
          >
            {{ $t("view.locationMap.showMap") }}
          </v-btn>
        </v-col>
      </v-row>
    </v-responsive>
    <v-snackbar
      :top="true"
      v-model="isOpenSnackbar"
      :color="snackbarColor"
      :timeout="1500"
    >
      {{ snackbarText }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="white"
          text
          v-bind="attrs"
          @click="isOpenSnackbar = false"
        >
          {{ $t("view.locationMap.close") }}
        </v-btn>
      </template>
    </v-snackbar>
  </v-responsive>
</template>

<script>
const LONGITUDE = 37.5213937;
const LATITUDE = 126.9838961;

export default {
  name: "LocationMap",
  data: () => ({
    isOpenSnackbar: false,
    snackbarColor: "",
    snackbarText: "",
    map: undefined,
    isInitializeGoogle: false,
    isAuthSignedIn: false,
  }),
  mounted() {
    this.createKakaoMap();
    // eslint-disable-next-line no-undef
    gapi.load("client:auth2", this.initGoogleCalendarAPI);
  },
  methods: {
    createKakaoMap() {
      this.createMap(LONGITUDE, LATITUDE);
      // eslint-disable-next-line no-undef
      const bounds = new kakao.maps.LatLngBounds();
      const markerOfWeddingLocation = this.createMarker(
        bounds,
        //36.11024731756055, 128.3097812883539
        36.11024731756055,
        128.3097812883539,
        "WED"
      );
      const markerOfStation = this.createMarker(
        bounds,
        36.11024731756055,
        128.3097812883539
      );

      markerOfWeddingLocation.setMap(this.map);
      markerOfStation.setMap(this.map);
      this.map.setBounds(bounds);

      // eslint-disable-next-line no-undef
      // const zoomControl = new kakao.maps.ZoomControl();
      // eslint-disable-next-line no-undef
      // this.map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
    },
    createMap(longitude, latitude) {
      const options = {
        // eslint-disable-next-line no-undef
        center: new kakao.maps.LatLng(longitude, latitude),
        level: 5,
      };

      // eslint-disable-next-line no-undef
      this.map = new kakao.maps.Map(this.$refs.mapContainer, options);
      // note: zoom disable
      // map.setZoomable(false);
    },
    createMarker(bounds, longitude, latitude, type) {
      // eslint-disable-next-line no-undef
      const position = new kakao.maps.LatLng(longitude, latitude);
      bounds.extend(position);

      let markerImage;

      if (type === "WED") {
        const imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png"; // 마커이미지의 주소
        // eslint-disable-next-line no-undef
        const imageSize = new kakao.maps.Size(24, 35); // 마커이미지의 크기
        // const imageOption = {offset: new kakao.maps.Point(27, 69)}; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.
        // const markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption);
        // eslint-disable-next-line no-undef
        markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);
      }

      // eslint-disable-next-line no-undef
      const marker = new kakao.maps.Marker({
        image: markerImage,
        position,
        map: this.map,
      });

      return marker;
    },
    initGoogleCalendarAPI() {
      const CLIENT_ID =
        "215390870050-722lfan2ekh51le7rsch65dnum8onjm8.apps.googleusercontent.com";
      const API_KEY = "AIzaSyDyYUTFYEpqD4c6Okb-qDE42shcSup0POQ";

      // Array of API discovery doc URLs for APIs used by the quickstart
      const DISCOVERY_DOCS = [
        "https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest",
      ];

      // Authorization scopes required by the API; multiple scopes can be
      // included, separated by spaces.
      const SCOPES = "https://www.googleapis.com/auth/calendar";
      // eslint-disable-next-line no-undef
      gapi.client
        .init({
          apiKey: API_KEY,
          clientId: CLIENT_ID,
          discoveryDocs: DISCOVERY_DOCS,
          scope: SCOPES,
        })
        .then(
          () => {
            this.isInitializeGoogle = true;

            // eslint-disable-next-line no-undef
            gapi.auth2
              .getAuthInstance()
              .isSignedIn.listen(this.setAuthSignedIn);

            // eslint-disable-next-line no-undef
            this.setAuthSignedIn(gapi.auth2.getAuthInstance().isSignedIn.get());
          },
          (error) => {
            this.isInitializeGoogle = false;

            console.error(`google api init fail.`, error);
            // console.error(JSON.stringify(error, null, 2));
          }
        );
    },
    isSignined() {
      // return false;
      // eslint-disable-next-line no-undef
      return gapi.auth2.getAuthInstance().isSignedIn.get();
    },
    setAuthSignedIn(isSignedIn) {
      this.isAuthSignedIn = isSignedIn;
    },
    onClickCalendarEvent() {
      // eslint-disable-next-line no-undef
      gtag("event", "add_calendar");
      if (this.isSignined()) {
        this.addNewEvent();
      } else {
        this.authSignin();
      }
    },
    addNewEvent() {
      const event = {
        summary: "김세화 ♡ 정량실 결혼식",
        location: "구미시 금오산호텔",
        description: "옷차림은 편하게, 마음은 가볍게, 10분일찍 오시면 좋아요",
        start: {
          dateTime: "2021-11-21T14:30:00+09:00",
          timeZone: "Asia/Seoul",
        },
        end: {
          dateTime: "2021-11-21T17:00:00+09:00",
          timeZone: "Asia/Seoul",
        },
        reminders: {
          useDefault: false,
          overrides: [
            { method: "popup", minutes: 60 }, // 1 hour
            { method: "popup", minutes: 1440 }, // 1 day
          ],
        },
      };
      // eslint-disable-next-line no-undef
      const request = gapi.client.calendar.events.insert({
        calendarId: "primary",
        resource: event,
      });

      request.execute(this.eventCallback);
    },
    async authSignin() {
      // eslint-disable-next-line no-undef
      await gapi.auth2.getAuthInstance().signIn();

      if (this.isSignined()) {
        this.addNewEvent();
      } else {
        this.snackbarText = this.$t("view.locationMap.identifyError");
        this.snackbarColor = "warning";
      }

      this.isOpenSnackbar = true;
    },
    eventCallback(event) {
      console.log("Event: ", event);

      if (!event.htmlLink) {
        this.snackbarText = this.$t("view.locationMap.eventCreateError");
        this.snackbarColor = "error";
        this.isOpenSnackbar = true;

        console.error("Event created fail: " + event.htmlLink);
        return;
      }

      this.snackbarText = this.$t("view.locationMap.eventCreateSuccess");
      this.snackbarColor = "primary";
      this.isOpenSnackbar = true;

      console.log("Event created: " + event.htmlLink);
    },
    handleSignoutClick() {
      // eslint-disable-next-line no-undef
      gapi.auth2.getAuthInstance().signOut();
    },
  },
};
</script>

